html{
	background-color: #F5F5F5;
}

header {
	background-color: #24292e;
	color: #fff;
	display: flex;
	justify-content: space-between;
	align-items: center;
	height: 80px;
	padding: 0 20px;
	position: fixed;
	top: 0;
	width: 100%;
	z-index: 1;
  }
  
  nav {
	display: flex;
	justify-content: space-between;
	align-items: center;
  }
  
  ul {
	list-style: none;
	margin: 0;
	padding: 0;
	display: flex;
  }
  
  li {
	margin-right: 20px;
	border: solid 2px #fff;
	border-radius: 5px;
	padding: 3px;
  }
  
  a {
	color: #fff;
	text-decoration: none;
	font-size: 18px;
  }
  
  a:hover {
	border-bottom: 2px solid #fff;
  }
  
  .banner {
	background-color: #f5f5f5;
	padding: 50px 0;
	text-align: center;
  }
  
  .banner-title {
	font-size: 36px;
	margin-bottom: 20px;
  }
  
  .banner-text {
	font-size: 20px;
	margin-bottom: 40px;
  }
  
  .banner-buttons {
	display: flex;
	justify-content: center;
	gap: 20px;
  }
  
  .button {
	border: none;
	border-radius: 4px;
	font-size: 16px;
	font-weight: 600;
	padding: 12px 20px;
	cursor: pointer;
  }
  
  .button-primary {
	
	background-color: #0077cc;
	padding: 10px;
	border-radius: 4px;
	color: #fff;
  }
  
  .button-secondary {
	border: 2px solid #0077cc;
	padding: 10px;
	border-radius: 4px;
	color: #0077cc;
  }
  
  .button:hover {
	opacity: 0.8;
  }
  

  .main-content {
	padding-top: 100px; /* adjust as needed */
	padding-bottom: 100px; /* adjust as needed */
  }
  
  
  
  h1 {
	font-size: 48px;
	margin: 0;
  }
  
  p {
	font-size: 24px;
	margin: 20px 0;
  }
  
  .button-container {
	display: flex;
	justify-content: center;
	align-items: center;
  }
  
  .gallery {
	background-color: #F5F5F5;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	margin-left: 10%;
	margin-right: 10%;
  }
  
  .gallery__image {
	width: 23%;
	margin-bottom: 20px;
  }
  .footer {
	background-color: #333;
	color: #fff;
	position: absolute;
	bottom: 0;
	width: 100%;
  }
  
  .container {
	max-width: 1200px;
	margin: 0 auto;
	padding: 20px;
  }
  
  .footer__text {
	font-size: 14px;
  }

  
  .App {
	position: relative;
	min-height: 100vh;
  }
  
  .main-content {
	padding-top: 100px;
	padding-bottom: 100px;
  }
  
  .Footer {
	position: absolute;
	width: 100%;
	height: 100px;
	background-color: #333;
	color: #fff;
	justify-content: center;
	align-items: center;
	z-index: 2;
  }
  